import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalService } from 'ng-zorro-antd/modal';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  Banner,
  CreateBannersGQL,
  GetBannerListGQL,
} from '../../../../graphql/graphql';
import { BannerDetailsComponent } from '../../components/banner-details/banner-details.component';

@Component({
  selector: 'app-banner-list',
  templateUrl: './banner-list.component.html',
  styleUrls: ['./banner-list.component.scss'],
})
export class BannerListComponent implements OnInit, OnDestroy {
  destroy$: Subject<boolean> = new Subject();
  banners: Banner[] = [];

  constructor(
    private getBannerListGQL: GetBannerListGQL,
    private msgService: NzMessageService,
    private modalService: NzModalService,
    private createBannersGQL: CreateBannersGQL,
    private translateService: TranslateService,
  ) {}

  ngOnInit(): void {
    this.getBannerListGQL.fetch().subscribe(({ data, error, errors }) => {
      if (error || errors) {
        return;
      }

      this.banners = [...data.banners];
    });
  }

  showBannerModal(): void {
    this.modalService.create({
      nzContent: BannerDetailsComponent,
      nzComponentParams: {
        banners: this.banners,
      },
      nzOnOk: (instance) => {
        this.banners = [...instance.banners];
      },
    });
  }

  dropBanner(event: CdkDragDrop<Banner[]>): void {
    moveItemInArray(this.banners, event.previousIndex, event.currentIndex);
    this.syncBanneres();
  }

  deleteBanner(index: number): void {
    this.banners = this.banners.filter((_, i) => index !== i);
    this.syncBanneres();
  }

  private syncBanneres(): Subscription {
    return this.createBannersGQL
      .mutate({
        data: this.banners.map((banner) => ({
          liveStreamId: banner.liveStreamId,
          thumbnailUrl: banner.thumbnailUrl,
        })),
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe(({ errors }) => {
        if (errors) {
          this.msgService.error(
            this.translateService.instant('ui.message.error'),
          );
          return;
        }
        this.msgService.success(
          this.translateService.instant('ui.message.success'),
        );
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
