import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzModalRef } from 'ng-zorro-antd/modal';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { Subject } from 'rxjs';
import { map, mergeMap, takeUntil } from 'rxjs/operators';

import { environment } from '../../../../environments/environment';
import { Banner, CreateBannersGQL } from '../../../../graphql/graphql';
import { FileService } from '../../../shared/services/file/file.service';

@Component({
  selector: 'app-banner-details',
  templateUrl: './banner-details.component.html',
  styleUrls: ['./banner-details.component.scss'],
})
export class BannerDetailsComponent implements OnInit, OnDestroy {
  destroy$: Subject<boolean> = new Subject();
  validateForm: FormGroup;
  thumbnailFile?: NzUploadFile;
  thumbnailUrl?: string;
  loading = false;
  banners: Banner[] = [];

  constructor(
    private fb: FormBuilder,
    public modal: NzModalRef,
    private createBannersGQL: CreateBannersGQL,
    private fileService: FileService,
    private httpClient: HttpClient,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.validateForm = this.fb.group({
      liveStreamId: [null, Validators.required],
      thumbnailUrl: [null, Validators.required],
    });
  }

  ngOnInit(): void {}

  beforeUploadThumbnail = (file: NzUploadFile): boolean => {
    this.thumbnailFile = file;
    this.fileService.getBase64((file as unknown) as File, (img: string) => {
      this.thumbnailUrl = img;
      this.validateForm.patchValue({
        thumbnailUrl: img,
      });
    });
    return false;
  };

  submitForm(): void {
    Object.values(this.validateForm.controls).forEach((control) => {
      control.markAsDirty();
      control.updateValueAndValidity();
    });

    if (this.validateForm.invalid) {
      return;
    }

    this.loading = true;
    const formData = new FormData();
    formData.append('file', (this.thumbnailFile as unknown) as File);
    this.httpClient
      .post<{ url: string }>(
        `${environment.apiUri}/single-upload?useWatermark=false`,
        formData,
      )
      .pipe(
        takeUntil(this.destroy$),
        mergeMap((value) => {
          const banners = [
            {
              liveStreamId: this.validateForm.controls.liveStreamId.value,
              thumbnailUrl: value.url,
            },
            ...this.banners.map((banner) => ({
              liveStreamId: banner.liveStreamId,
              thumbnailUrl: banner.thumbnailUrl,
            })),
          ];
          return this.createBannersGQL
            .mutate({
              data: banners,
            })
            .pipe(map(({ errors }) => (errors ? this.banners : banners)));
        }),
      )
      .subscribe((banners) => {
        this.msgService.success(
          this.translateService.instant('ui.message.success'),
        );
        this.banners = banners;
        this.modal.destroy(banners);
        this.modal.triggerOk();

        this.loading = true;
      });
  }

  destroyModal(): void {
    this.modal.destroy();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
